import React, { useContext } from 'react';

import { AppContext } from '../../contexts/AppContext/AppContext';

import FullSizeVideoBox from '../user-video-box/FullSizeVideoBox';
import SmallSizeVideoBox from '../user-video-box/SmallSizeVideoBox';

function DualVideoLayout(classNames) {
  const { 
    focusedUserVideo,
    focusLocalUserVideo,
    focusGuestUserVideo,
    splitUserVideos,
    myName,
    guestName,
    peerConnection
  } = useContext(AppContext);

  const handleFullSizeVideoClick = () => {
    splitUserVideos();
  }

  const handleSmallSizeVideoClick = () => {
    if (focusedUserVideo === 'local') {
      focusGuestUserVideo();
    } else {
      focusLocalUserVideo();
    }
  }

  return (
    <div className="relative flex flex-wrap content-center justify-center w-full h-full video-area">

      { focusedUserVideo === 'local' &&
        <FullSizeVideoBox
          user={ 'local' }
          name={ myName }
          color="bg-primary"
          onClickVideo={ splitUserVideos }
        >
          { peerConnection &&
            <div className='absolute top-3 right-3 w-auto justify-end flex'>
              <SmallSizeVideoBox
                user={ 'guest' }
                name={ guestName }
                color="bg-primary"
                onClickVideo={ focusGuestUserVideo }
              />
            </div>
          }
        </FullSizeVideoBox>
      }

      { focusedUserVideo === 'guest' &&
        <FullSizeVideoBox
          user={ 'guest' }
          name={ guestName }
          color="bg-primary"
          onClickVideo={ splitUserVideos }
        >
          { peerConnection &&
            <div className='absolute top-3 right-3 w-auto justify-end flex'>
              <SmallSizeVideoBox
                user={ 'local' }
                name={ myName }
                color="bg-primary"
                onClickVideo={ focusLocalUserVideo }
              />
            </div>
          }
        </FullSizeVideoBox>
      }

    </div>
  );
}

export default DualVideoLayout;